*{
  padding: 0;
  margin:0;
 
  box-sizing: border-box;
  text-decoration: none;
}

.container{
  margin-top: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container button{
  width: 100px;
  height: 30px;
  border-radius: 20px;
  border: none;
  outline: none;
  background-color: red;
  color: #fff;
  font-size: 1.2rem;
  line-height: 8px;
  box-shadow: 0 10px 15px rgba(0,0,0, .8);
  cursor: pointer;

}

.container button:hover{
  font-size: 1.5rem;
}

.cookies-box{
  width: 350px;
  position: fixed;
  bottom:100px; left:-400px;
  z-index: 1000;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 15px rgba(0,0,0, .2);
  text-align: center;
  padding: 30px;
}

.cookies-box.active{
  left: 20px;
}

.cookies-box img{
  height: 110px;
  animation: image 10s linear infinite;
}

@keyframes image {
  0%{
    transform: rotate(360deg);
  }
  
}

.cookies-box h3{
  color: #333;
  font-size: 23px;
  padding: 5px 0;
}

.cookies-box p{
  color: #666;
  font-size: 13px;
  padding: 5px 0;
  line-height: 1.8;
}

.cookies-box .accept{
  background: #FBB45F;
  color: #fff;
  border-radius: 5px;
  display: block;
  margin: 15px 0;
  padding: 10px;
}
.cookies-box .accept{
  background: #FBB45F;
  color: #fff;
  border-radius: 5px;
  display: block;
  margin: 15px 0;
  padding: 10px;
}
.cookies-box .setting{
  color: #666;
}

.cookies-box .close_btn{
  position:absolute;
  top:-2px; right: 12px;
  font-size: 40px;
  color: #999;
  cursor: pointer;
}

.cookies-box .close_btn:hover{
  color: #333;
}